@import './footer/index';
@import './nav/index';
@import './color';

html, body {
  color: #fff;
  line-height: 2rem;
  -webkit-align-items: center;  
  -ms-flex-align: center;  
  align-items: center;
  font-family: Helvetica, "Microsoft Jhenghei", Arial, "Raleway", Geneva, sans-serif;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.12);
  h1, h2, h3, h4, h5, h6, p {
    font-family: Helvetica, "Microsoft Jhenghei", Arial, "Raleway", Geneva, sans-serif;
  }
  a {
    color: #2EBAFF;
    text-decoration: none !important; 
    &:hover {
      text-decoration: none; 
      opacity: 0.8;
    }
  }
}
.container {
  display: flex;
}
#root {
  margin: 17px;
  padding: 30px;
  min-height: 500px;
}
.error {
  position: fixed;
  width: 100%;
  text-align: center;
  top: 0px;
  z-index: 9999;
  color: #FFFFFF;
  background: #009688;
  line-height: 38px;
  a {
    text-decoration: none; 
    color: #fff;
    display: block;
    width: 100%;
    text-align: center;
    margin: auto;

    &:hover {
    color: #fff;
    opacity: 0.8;
    }
  }
}
.background-cover {
  /*background-color: rgba(217, 238, 255, 0.79);*/
  background-color: rgba(0, 0, 0, 0.12);
  height: 100vh;
  z-index: -1;
  top: 0;
  width: 100vw;
  position: fixed;
  -moz-box-shadow: rgba(0, 0, 0, 0.8) 5px 5px 85px inset;
  -webkit-box-shadow: rgba(0, 0, 0, 0.8) 10px 5px 85px inset;
  box-shadow: rgba(0, 0, 0, 0.8) 10px 5px 85px inset;
}

#background {
  position: fixed;
  top: 0;
  z-index: -2;
  width: 100%;
  height: 100%;
  color: #fff;
  background: url(https://c2.staticflickr.com/8/7214/13717611224_a31fb184ff_b.jpg),#fff;
  background-size: cover!important;
  background-position: center center!important;
  -webkit-animation: fadein .5s ease 0s forwards;
}
#sidebar {
  left: 10%;
  list-style: none;
}

#weather {
  text-align: center;
  bottom: 30px;
  right: 15px;
  position: fixed;
}


::-webkit-input-placeholder { /* WebKit, Blink, Edge */
  color: rgba(255, 255, 255, 0.65);
}
:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
  color: rgba(255, 255, 255, 0.65);
  opacity:  1;
}
::-moz-placeholder { /* Mozilla Firefox 19+ */
  color: rgba(255, 255, 255, 0.65);
  opacity:  1;
}
:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: rgba(255, 255, 255, 0.65);
}
:placeholder-shown { /* Standard (https://drafts.csswg.org/selectors-4/#placeholder) */
  color: rgba(255, 255, 255, 0.65);
}

.btn {
  &:hover {
    color: #fff;
    opacity: 0.8;
  }
  &:focus {
    color: #fff;
  }
}


@media all and (max-width: 1180px) {
  .navbar-header {
    left: 20px;
  } 
  #sidebar {
    left: 5px;

  }
  #weather {
    margin-right: 0px;
  }
}

@media all and (max-width: 1000px) {
   #weather {
      display: none;
   }
   #sidebar {
      padding-left: 3px;
   }
}
@media all and (max-width: 700px) {
  .nav-center {
    display: none;
  }

  #footer-right {
    display: none;
  }
  #sidebar i {
    font-size: 1rem;
  }
}
