.author__wrapper {
  text-align: center; position:fixed; left: 10px; bottom: 5px; font-size: 1rem; z-index: 999; opacity: 0.8;
}

#footer-right {
  padding: 5px;
  position: fixed;
  right: 10px;
  bottom: 0px;
}

#footer-right div {
  font-size: 1rem;
  width: 100%;
}

#footer-right i {
  cursor: pointer;
  top: 0;
  padding-left: 5px;
}

#footer-right #contact {
  text-shadow: none;
  display: none;
  background: rgba(0, 0, 0, 0.24);
  padding: 10px;
  border-radius: 10px;
  width: 200px;
  position: fixed;
  bottom: 45px;
  right: 45px;
  color: #fff;
}

#footer-right .message-header {
  padding: 10px;
  margin-bottom: 15px;
  margin-top: 10px;
  font-size: 1.4rem;
}

#contact input, #contact textarea{
  outline: none;
  background: transparent;
  border: none;
  border-bottom: 1px solid #fff;
  display: block;
  margin: auto;
  resize: none;
  width: 90%;
}

#footer-right .close-contact {
  cursor: pointer;
  position: relative;
  float: right;
  display: block;
  width: 15px;
}

#contact .btn {
  background-color: rgba(51, 122, 183, 0);
  border-color: #FFFFFF;
}
#contact .btn:hover {
  opacity: 0.8;
  color: #fff;
}


